1. 系统介绍
本系统为我司统一采购的数据建模平台，简称DDM。该系统主要能力主要包括：
主流数据库模型建模
数据模型可视化
多人协作在线建模
数据模型版本管控
2. 系统环境说明
为了保证系统的正常使用，并能够提供更多的同事使用（包括自有、外包人员），将日常开发、调试环境与正式环境分开，数据建模平台DDM部署了生产和测试两套环境，两套环境安装包一致，功能完全相同，升级也是同步进行，两者区别见下表：
环境名称	生产环境	测试环境
使用人员	兴证员工	兴证员工、厂商外包人员
环境功能	数据模型正式发布环境，建议由测试环境同步而来，一般不直接在该环境设计模型
以系统为单位，系统目录由项目组申请，数据治理组创建，登记各系统生产环境最新的数据模型
各团队自行保证该环境模型的准确性，数据治理组会定期对生产环境数据模型质量进行评估	用于登记系统在设计、开发阶段中的数据模型设计中间稿
各团队自行保证该环境模型的准确性，数据治理组对该环境数据模型无硬性要求
账户及权限	经过邮件申请后，可为自有人员开通账户，仅查询权限
以团队为单位创建1-2个维护权限账户，用于维护生产环境数据模型	经过邮件申请后，可为自有人员及外包人员开通账户，可区分维护及查询权限
若外包人员无法访问服务器，需通过ITSM申请网段访问权限
