			<uni-collapse-item title="三、数据库建模基础"  >
				<text>
1.新建模型
DDM目前支持逻辑模型和物理模型建模，其中物理模型又分和关系型数据模型和非关系
型数据模型，支持的关系型数据库类型有MariaDB、DB2、GaussDB、GBase、SAPHANA、MaxCompute、MySQL、Oracle、PostgreSQL、SQLServer和Teradata等，支持的非关系型数据库有Cassandra、HIVE和MongoDB等。
2.表设计		
右键模型设计区域已经存在的表，在弹出的表编辑器菜单中选择【编辑表】选项（或者双击模型设计区域已存在的表名）后会弹出表编辑器的对话框。
3编辑表
在模型作业区的任意表上右键，选择【编辑表】选项（或者双击模型设计区域已存在的表名）即可进入表编辑器对话框，表编辑器可以对表进行新增、删除、编辑中文名和英文名、翻译功能等等。同时也可以通过表编辑器快速进入字段编辑器和索引编辑器。				
4删除表
DDM目前提供三种删除表（实体）的方式。
第一种，从左侧模型树上右键一张表，选择【删除】选项。				
				</text>
			</uni-collapse-item>
			<uni-collapse-item title="四、数据标准管理"  >
				<text>
1.数据标准

1.1落地数据标准
在部署有DAM数据资产管理平台的情景下，用户每次使用DDM客户端登录模型库，会将
服务端最新的数据标准同步到DDM客户端本地。
用户在进行建模时，在【字段编辑器】中可检索出相匹配的数据标准，点击该数据标准即可将数据标准中的信息下落到字段中去。同时双击该数据标准名称上会显示当前数据标准的详情。
1.2数据标准管理

在【模型】选项的工具栏，点击【数据标准】即可打开【数据标准浏览器】。
在【数据标准浏览器】中，点击【同步更新】可以从服务器更新数据标准。
【全部发布】和【发布所选目录/标准】可以发布私有的数据标准。
2.发现和一致性检查
在【模型】选项的工具栏，点击【发现】即可打开【发现可能的数据标准】。即在设计的过程中字段未关联数据标准，可以通过该功能对已有的模型字段进行批量的数据标准的关联。
在【模型】选项的工具栏，点击【一致性检查】即可打开【数据标准一致性检查】。即对已经关联数据标准的字段进行字段与数据标准的差异性校验。
在【选项设置】中，可以勾选检查时比对的信息项。
				</text>
			</uni-collapse-item>
			<uni-collapse-item title="五、模型库管理"  >
				<text>
1.模型浏览器
【模型库】选项的工具栏，点击【打开模型库】可以打开【模型浏览器】。
可以查看模型库目录，关键字搜索目录，查看版本等功能
2.模型回收站
模型回收站负责管理模型库中已经被删除的模型， 包括显示已删除模型的模型名称、模型分支、模型目录、删除时间、删除者等信息。 
在登录到模型库的状态下，切换到【模型库】菜单栏，点击工具栏上的【模型回收站】按钮，即可进入删除模型管理页面。 点击每个模型后面的【还原】按钮，即可还原该模型。 
				</text>
